
import { defineComponent } from 'vue'
import Tileset from './Tileset.vue'

export default defineComponent({
  name: 'Block',
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  components: {
    Tileset
  },
  computed: {
    leftVal() {
      return Math.floor(this.value / 10)
    },
    rightVal() {
      return this.value % 10
    }
  }
})
