
import { defineComponent } from 'vue'
import Tile from './Tile.vue'

export default defineComponent({
  name: 'Tileset',
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  components: {
    Tile
  },
  computed: {
    states() {
      const binary = this.value.toString(2).padStart(4, '0').split('')
      return binary.map(val => val === '1')
    }
  }
})
